import {Injectable, Optional} from '@angular/core';

import {AdaptBusyConfig} from './busy-config';
import {AdaptTranslateService} from '../common/i18n';

@Injectable({
  providedIn: 'root'
})
export class AdaptBusyService {
    config: AdaptBusyConfig;

    constructor(@Optional() config: AdaptBusyConfig,
                translateService: AdaptTranslateService) {
        if (config) {
          this.config = config;
        } else {
          this.config = new AdaptBusyConfig();
          // Default message should be translated
          this.config.message = translateService.getCurrentLanguage()[this.config.message];
        }
    }
}
